<template>
    <div class="field-container">
        <a-image :width="imgWidthSize + 10" :height="imgWidthSize" :src="imgSrc"
            fallback="data:image/png;base64," />
        <div class="img-tool">
            <FilesUpload :success-callback="handleChange">
                <a-button :icon="h(CloudUploadOutlined)">上传图片</a-button>
            </FilesUpload>
            <CropperModal v-model:showModal="showModal" :imgSrc="$props.value.replace('https', 'http')"
                :success-callback="handleChange">
                <a-button :icon="h(ScissorOutlined)" @click="showModal = true"
                    :disabled="value.length === 0">裁剪图片</a-button>
            </CropperModal>
            <a-button v-if="isNeedDelete" :icon="h(DeleteOutlined)" danger :disabled="value.length === 0"
                @click="$emit('update:value', '')">删除图片</a-button>
        </div>
    </div>

</template>

<script setup lang='ts'>
import { h, computed, ref } from 'vue'
import { CloudUploadOutlined, ScissorOutlined, DeleteOutlined } from '@ant-design/icons-vue'

const $props = defineProps<{
    value: string,
    isNeedDelete?: boolean
}>()

// 模态框
const showModal = ref<boolean>(false)

// 回显图片
const imgSrc = computed(() => {
    // return $props.isNeedDelete ? $props.value : getImgSrc($props.value)
    return ($props.value).replace('https', 'http')
})

const imgWidthSize = computed(() => {
    return ($props.isNeedDelete ? 115 : 75)
})

const $emit = defineEmits<{
    (e: 'update:value', param: string): void
}>()

const handleChange = (param: string | { urls: string[] }) => {
    if (typeof param !== 'string') {
        $emit('update:value', param.urls[0].replace('https', 'http'))
    } else {
        $emit('update:value', param.replace('https', 'http'))
    }
}

</script>

<style scoped lang='scss'>
.field-container {
    display: flex;
    align-items: center;
    gap: 15px;

    .img-tool {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;

    }
}

::v-deep() .ant-image .ant-image-img {
    object-fit: cover;
}

::v-deep() .ant-image {
    // height: 86px !important;
    border-radius: 10px;
    overflow: hidden;
    border: 1px rgb(122, 122, 122, .5) dashed;
}
</style>
